import React from 'react'
import './navbar.css'
import {NavLink } from 'react-router-dom'

const Navbar = () => {
    return (
        <> 
        <div className=" Container-fluid nav-bg">
            <div className='row'>
                <div className='col-10 mx-auto'>


                    <nav className="navbar navbar-expand-lg navbar-light">
                        <div className="container-fluid">
                            <NavLink className="navbar-brand nav-link" to="/">E Locals </NavLink >

                            <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                                <span class="navbar-toggler-icon"></span>
                            </button>


                            <div className="collapse navbar-collapse" id="navbarSupportedContent">
                                <ul className="navbar-nav ml-auto mb-2 mb-lg-0">
                                    <li className="nav-item">
                                        <NavLink exact activeClassName="link_active" className="nav-link " aria-current="page" to="/">Why Elocals</NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink activeClassName="link_active" className="nav-link" to="/calculator">Commission Calculator</NavLink>
                                    </li>
                                    <li className="nav-item">
                                        <NavLink activeClassName="link_active" className="nav-link" to="/contact">Contact Us </NavLink>
                                    </li>
                                    
                                    <li className="nav-item">
                                        <NavLink activeClassName="link_active" className="nav-link btn-primary sellstyle" style={{color:"white"}} to="/login">Seller Login</NavLink>
                                    </li>



                                </ul>
                            </div>
                        </div>
                    </nav>
                </div>

            </div>

        </div>
    </>
    )
}

export default Navbar 

